<script>
    import Header from "./Header.vue";
    import DaysOfWeek from "./DaysOfWeek.vue";
    import monthBody from "../mixins/monthBody";

    export default {
        mixins:[monthBody],
        components: {
            appDays: DaysOfWeek
        },
        methods:{
          dayCheck(arr){
              let todayDay = Number(new Date().toString().split(' ')[2]);
              let currentMonth = Boolean;
              this.counter!==0?currentMonth = false:currentMonth=true;
              return arr === todayDay && currentMonth
          }
        },

        created() {

            this.firstDay();
            this.lastDayCurrMonth();
            this.lastDayPrevMonth();
            this.prevDaysFiller();
            this.currentDaysFiller();
            this.restDaysFiller();

        },
        beforeUpdate() {
            this.firstDay();
            this.lastDayCurrMonth();
            this.lastDayPrevMonth();
            this.prevDaysFiller();
            this.currentDaysFiller();
            this.restDaysFiller();
        },

        watch: {
            counter() {

            }
        }

    }
</script>

