import * as types from '../types';

const state = {
	counter: 0
};

const getters = {
	[types.COUNTER]: state => {
		return state.counter;
	}
};

const mutations = {
	[types.MUTATE_COUNTER_INCREMENT]: (state, payload) => {
		state.counter += payload;
	},
	[types.MUTATE_COUNTER_DECREMENT]: (state, payload) => {
		state.counter -= payload;
	}
};
export default {
	state,
	getters,
	mutations
}