import DaysOfWeek from "../Components/DaysOfWeek.vue";
import * as types from '../store/types';
import {mapGetters} from 'vuex';

export default {
    data() {
        return {
            // mCount: counter.state.counter,

            days: DaysOfWeek.data().daysOfWeek,
            date: new Date(),
            month: new Date().getMonth(),
            monthBeginsDay: null,
            monthBeginsNumber: null,
            prevMonthEndsDay: null,
            prevMonthEndsNumber: null,
            currentMonthEndNum: null,
            currentMonthEndDay: null,
            daysOfPrev: [],
            daysOfCurrent: [],
            daysAtTheEnd: []
        }
    },
    computed: {
        ...mapGetters({
            counter: types.COUNTER
        })
    },
    methods: {

        firstDay() {
            let fDay = new Date(this.date.getFullYear(), this.month + this.counter, 1);
            this.monthBeginsDay = String(fDay).split(' ')[0];
            this.monthBeginsNumber = Number(String(fDay).split(' ')[2]);

        },
        lastDayCurrMonth() {
            let lDays = new Date(this.date.getFullYear(), this.month + 1 + this.counter, 0);
            this.currentMonthEndNum = Number(String(lDays).split(' ')[2]);
            this.currentMonthEndDay = String(lDays).split(' ')[0]
        },
        lastDayPrevMonth() {
            let lDay = new Date(this.date.getFullYear(), this.month + this.counter, 0);
            this.prevMonthEndsDay = String(lDay).split(' ')[0];
            this.prevMonthEndsNumber = Number(String(lDay).split(' ')[2]);

        },
        prevDaysFiller() {
            this.daysOfPrev = [];
            let initNum = this.prevMonthEndsNumber;
            while (this.daysOfPrev.length <= (this.days.indexOf(this.prevMonthEndsDay))) {
                this.daysOfPrev.unshift(initNum);
                initNum--
            }
        },
        currentDaysFiller() {
            this.daysOfCurrent = [];
            let initNum = 0;
            while (this.daysOfCurrent.length < this.currentMonthEndNum) {
                initNum++;
                this.daysOfCurrent.push(initNum);
            }
        },
        restDaysFiller() {
            this.daysAtTheEnd = [];
            const days = 42 - (this.daysOfPrev.length + this.daysOfCurrent.length);
            for (let i = 1; i <= days; i++) {
                this.daysAtTheEnd.push(i);
            }

        },


    },
}